@import 'https://fonts.googleapis.com/css?family=Inconsolata';

.page-not-found {
  min-height: 100%;

  body {
    box-sizing: border-box;
    height: 100%;
    background-color: #000;
    background-image: radial-gradient(#11581e, #041607),
      url('https://media.giphy.com/media/oEI9uBYSzLpBK/giphy.gif');
    background-repeat: no-repeat;
    background-size: cover;
    font-family: 'Inconsolata', Helvetica, sans-serif;
    font-size: 1.5rem;
    color: rgba(128, 255, 128, 0.8);
    text-shadow: 0 0 1ex rgba(51, 255, 51, 1),
      0 0 0.125rem rgba(255, 255, 255, 0.8);
  }

  header,
  footer {
    display: none;
  }

  .noise {
    pointer-events: none;
    background-image: url('https://media.giphy.com/media/oEI9uBYSzLpBK/giphy.gif');
    background-repeat: no-repeat;
    background-size: cover;
    z-index: -1;
    opacity: 0.02;
  }

  .overlay {
    pointer-events: none;
    background: repeating-linear-gradient(
      180deg,
      rgba(0, 0, 0, 0) 0,
      rgba(0, 0, 0, 0.3) 50%,
      rgba(0, 0, 0, 0) 100%
    );
    background-size: auto 0.25rem;
    z-index: 1;
    &::before {
      content: '';
      pointer-events: none;
      position: absolute;
      display: block;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background-image: linear-gradient(
        0deg,
        transparent 0%,
        rgba(32, 128, 32, 0.2) 2%,
        rgba(32, 128, 32, 0.8) 3%,
        rgba(32, 128, 32, 0.2) 3%,
        transparent 100%
      );
      background-repeat: no-repeat;
      animation: scan 7.5s linear 0s infinite;
    }
  }

  .terminal {
    padding: 8rem 4rem;
    text-transform: uppercase;
    line-height: 1.5;
  }

  .output {
    color: rgba(128, 255, 128, 0.8);
    text-shadow: 0 0 0.0625rem rgba(51, 255, 51, 0.4),
      0 0 0.125rem rgba(255, 255, 255, 0.8);
    &::before {
      content: '> ';
    }
  }

  h1 {
    font-size: 4rem;
  }

  p {
    font-size: 1.5rem;
  }

  a {
    color: #fff;
    text-decoration: none;
    &::before {
      content: '[';
    }
    &::after {
      content: ']';
    }
  }

  .errorcode {
    color: white;
  }

  .noise,
  .terminal,
  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
}

@keyframes scan {
  0% {
    background-position: 0 -100vh;
  }
  35%,
  100% {
    background-position: 0 100vh;
  }
}
